.form-login {
  margin: 10% auto 0;

  h2 {
    text-align: center;
  }

  form {
    padding: 2rem;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0px 15px 15px 5px rgba(187, 218, 247, .25);

    a {
      color: inherit;
      text-decoration: none;
      
      &:hover,
      &:focus {
      text-decoration: underline;

      }
    }

    img {
      margin-bottom: 10px;
      
    }

    .form-actions {

      button {
        width: 100%;
      }
    }

    .reset-password {
      text-align: center;
      margin-top: 10px;
      font-size: 0.8rem;
    }
  }
}

.page.two-factor {

  .page-header {
    margin: 0 0 0 -15px;
    padding-left: 15px;

  }

  form {
    margin-top: 60px;

  }
}